import React from 'react';

function NodePanel({ onAddNode, compact = false }) {

  const nodeGroups = {
    '': [
      { type: 'http-request', icon: '', label: 'API', description: 'HTTP API' },
      { type: 'condition', icon: '', label: '', description: '' },
      { type: 'if-condition', icon: '', label: 'IF', description: 'IF-ELSE' },
      { type: 'switch', icon: '', label: 'Switch', description: '' },
      { type: 'data-map', icon: '', label: '', description: '' },
      { type: 'notification', icon: '', label: '', description: '' }
    ],
    'LINE': [
      { type: 'line-push', icon: '', label: 'LINE', description: 'LINE' },
      { type: 'line-reply', icon: '', label: 'LINE', description: 'LINE' },
      { type: 'line-carousel', icon: '', label: 'LINE', description: '' }
    ],
    '': [
      { type: 'webhook-trigger', icon: '', label: 'Webhook', description: '' },
      { type: 'program-entry', icon: '', label: '', description: '' }
    ],
    '': [
      { type: 'existing-workflow', icon: '', label: '', description: '' }
    ]
  };

  const handleDragStart = (e, nodeType) => {
    e.dataTransfer.setData('application/reactflow', nodeType.type);
    e.dataTransfer.effectAllowed = 'move';
  };

  const addNodeDirectly = (nodeType) => {
    const defaultData = {
      'http-request': { label: 'API', url: '', method: 'GET' },
      'condition': { label: '', field: '{message}', operator: 'contains', value: '' },
      'if-condition': { label: 'IF', conditions: [{ field: '{message}', operator: 'contains', value: '' }], logic: 'AND' },
      'switch': { label: 'Switch', switchField: '{message}', cases: [{ value: '', label: '' }, { value: '', label: '' }], defaultCase: '' },
      'data-map': { label: '', name: '', mappings: [{from: '', to: ''}] },
      'line-push': { 
        label: 'LINE', 
        name: '',
        url: 'https://api.line.me/v2/bot/message/push', 
        method: 'POST',
        useDataFrom: 'custom',
        headers: { 'Authorization': 'Bearer ', 'Content-Type': 'application/json' },
        body: { to: '', messages: [{ type: 'text', text: '' }] }
      },
      'line-reply': { 
        label: 'LINE', 
        name: '',
        url: 'https://api.line.me/v2/bot/message/reply', 
        method: 'POST',
        useDataFrom: 'custom',
        headers: { 'Authorization': 'Bearer ', 'Content-Type': 'application/json' },
        body: { replyToken: '', messages: [{ type: 'text', text: '' }] }
      },
      'line-carousel': {
        label: 'LINE',
        name: '',
        headers: { 'Authorization': 'Bearer ', 'Content-Type': 'application/json' },
        body: {
          replyToken: '{replyToken}',
          messages: [{
            type: 'template',
            altText: '',
            template: {
              type: 'carousel',
              columns: [
                {
                  title: '1',
                  text: '1',
                  actions: [{ type: 'message', label: '1', text: '1' }]
                }
              ]
            }
          }]
        }
      },
      'webhook-trigger': { label: 'Webhook', name: '', description: '' },
      'program-entry': { label: '', name: '', description: '' },
      'notification': { label: '', message: '' },
      'existing-workflow': { label: '', workflowId: '', workflowName: '' }
    };
    
    onAddNode(nodeType, defaultData[nodeType] || { label: nodeType });
  };

  return (
    <div className="node-panel">
      {!compact && <h3> </h3>}
      {!compact && (
        <p style={{fontSize: '12px', color: '#666', margin: '0 0 15px 0'}}>
          
        </p>
      )}
      
      <div className={`node-library ${compact ? 'compact' : ''}`}>
        {Object.entries(nodeGroups).map(([groupName, nodes]) => (
          <div key={groupName} className="node-group">
            {!compact && <div className="group-title">{groupName}</div>}
            {nodes.map((nodeType) => (
              <div
                key={nodeType.type}
                className={`draggable-node ${compact ? 'compact' : ''}`}
                draggable
                onDragStart={(e) => handleDragStart(e, nodeType)}
                onClick={() => addNodeDirectly(nodeType.type)}
                title={compact ? nodeType.label : ''}
              >
                <div className="node-icon">{nodeType.icon}</div>
                {!compact && (
                  <div className="node-info">
                    <div className="node-title">{nodeType.label}</div>
                    <div className="node-desc">{nodeType.description}</div>
                  </div>
                )}
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
}

export default NodePanel;