import React, { useState } from 'react';
import WorkflowList from './WorkflowList';
import TokenManager from './TokenManager';

function WindowManager({ 
  onSelectWorkflow, 
  onNewWorkflow, 
  currentWorkflowId,
  compact = false,
  showNotification
}) {
  const [showWorkflowWindow, setShowWorkflowWindow] = useState(false);
  const [showTokenWindow, setShowTokenWindow] = useState(false);

  return (
    <>
      {/*  */}
      <div className={`toolbar ${compact ? 'compact' : ''}`}>
        <button 
          onClick={() => setShowWorkflowWindow(true)}
          className="toolbar-btn"
          title={compact ? '' : ''}
        >
           {!compact && ''}
        </button>
        <button 
          onClick={() => setShowTokenWindow(true)}
          className="toolbar-btn"
          title={compact ? 'Token' : ''}
        >
           {!compact && 'Token'}
        </button>
      </div>

      {/*  */}
      {showWorkflowWindow && (
        <div className="window-overlay">
          <div className="window">
            <div className="window-header">
              <h3> </h3>
              <button 
                onClick={() => setShowWorkflowWindow(false)}
                className="window-close-btn"
              >
                
              </button>
            </div>
            <div className="window-content">
              <WorkflowList 
                onSelectWorkflow={(id) => {
                  onSelectWorkflow(id);
                  setShowWorkflowWindow(false);
                }}
                onNewWorkflow={() => {
                  onNewWorkflow();
                  setShowWorkflowWindow(false);
                }}
                currentWorkflowId={currentWorkflowId}
                showNotification={showNotification}
              />
            </div>
          </div>
        </div>
      )}

      {/* Token */}
      {showTokenWindow && (
        <div className="window-overlay">
          <div className="window">
            <div className="window-header">
              <h3> Token</h3>
              <button 
                onClick={() => setShowTokenWindow(false)}
                className="window-close-btn"
              >
                
              </button>
            </div>
            <div className="window-content">
              <TokenManager />
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default WindowManager;